body {
  font-family: sans-serif;
  margin: 0;
  overflow: hidden;
  background: #2e1711;
  background: -moz-radial-gradient(center, ellipse cover, #2e1711 0%, #0e0906 100%);
  background: -webkit-radial-gradient(center, ellipse cover, #2e1711 0%, #0e0906 100%);
  background: radial-gradient(ellipse at center, #2e1711 0%, #0e0906 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#2e1711', endColorstr='#0e0906', GradientType=1);
}
.container {
  width: 800px;
  margin: 0 auto;
  padding: 20px;
}
* {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}
.hide {
  display: none;
}
.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -50px;
  width: 100px;
  text-align: center;
  display: inline-block;
  padding: 20px;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  text-shadow: 1px 1px 0 #000;
  display: none;
}
@media (min-width: 1310px) {
  body:before {
    content: 'Desktop';
    position: fixed;
    left: 10px;
    bottom: 10px;
    display: inline-block;
    padding: 10px;
    background: orange;
    color: #fff;
  }
}
@media (max-width: 1310px) {
  body:before {
    content: 'Tablet';
    position: fixed;
    left: 10px;
    bottom: 10px;
    display: inline-block;
    padding: 10px;
    background: orange;
    color: #fff;
  }
}
@media (max-width: 990px) {
  body:before {
    content: 'Portreit';
    position: fixed;
    left: 10px;
    bottom: 10px;
    display: inline-block;
    padding: 10px;
    background: orange;
    color: #fff;
  }
}
@media (max-width: 670px) {
  body:before {
    content: 'Mobile';
    position: fixed;
    left: 10px;
    bottom: 10px;
    display: inline-block;
    padding: 10px;
    background: orange;
    color: #fff;
  }
}
